<template>
	<div class="topnav">
		<el-row :gutter="20">
		  <el-col :span="16">
			  <el-row>
				<div class="logotext">因学教育总平台</div>
			  </el-row>
		  </el-col>
		  <el-col :span="8" >
			  <div class="topnavsz">
				  <el-dropdown class="topnavsz1"  @command="handleCommand">
				    <span class="el-dropdown-link">
				      <i class="el-icon-setting" style="font-size: 26px; color: #fff;padding: 5px;"></i>
				    </span>
				    <el-dropdown-menu slot="dropdown">
				      <el-dropdown-item command="a">管理员</el-dropdown-item>
				      <el-dropdown-item command="b">loading</el-dropdown-item>
				      <el-dropdown-item command="c">其他内容</el-dropdown-item>
				      <el-dropdown-item command="d" divided>推出登录</el-dropdown-item>
				    </el-dropdown-menu>
				  </el-dropdown>
				  <i class="el-icon-message topnavsz2" style="font-size: 26px; color: #fff;padding: 5px;"></i>
				  <i class="el-icon-chat-line-round topnavsz2" style="font-size: 26px; color: #fff;padding: 5px;"></i>
			  </div>
		  </el-col>
		</el-row>
	</div>
</template>

<script>
	 export default {
	    methods: {
	      handleCommand(command) {
	       console.log(command);
		   this.$router.push({path: '/Login'});
	      }
	    }
	  }
</script>

<style scoped lang="less">
	@import '../../assets/style/index.less';//颜色样式
	.topnav{
		padding: 0 40px;
		height: 70px;
		background-color:@color2;
		color: @colorbai;
		cursor: pointer;
		.icnsty{
			width: 40px;
			height: 40px;
			line-height: 40px;
			text-align: center;
			font-size: 26px;
			margin: 0 auto;
			display: block;
		}
		.icntext{
			width: 100%;
			float: left;
			text-align: center;
			font-size: 14px;
		}
	}
	.topnavsz{
		width:100%;
		height: 36px;
		padding: 17px 0;
		.topnavsz1{
			float: right;
		}
		.topnavsz2{
			float: right;
			margin-right: 20px;
		}
	}
	.logotext{
		font-size:24px;
		line-height: 70px;
	}
</style>



