<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text">财务设置</span>
		</div>
		<div class="InBoxBody-con">
			<el-divider content-position="left">分佣金设置</el-divider>
			<el-form :inline="true" class="demo-form-inline">
			  <el-form-item :label="item.name" v-for="(item,index) in fenrunONCO" :key="index">
			    <el-input v-model="item.Proportion" placeholder="请输入比例"></el-input>
			  </el-form-item>
			  <el-button type="primary" @click="onSubmit">设置</el-button>
			</el-form>
		    <el-divider content-position="left">体验课名额比例设置</el-divider>
			<el-form :inline="true" class="demo-form-inline">
			  <el-form-item label="体验课名额比例设置:">
			    <el-input v-model="minerCO.price" placeholder="请输入比例 /人"></el-input>
			  </el-form-item>
			  <el-button type="primary" @click="onSubmittyk">设置</el-button>
			</el-form>
			<el-divider content-position="left">单独体验课名额调整</el-divider>
			<div>
				<el-form :inline="true" :model="formInline" class="demo-form-inline">
				  <el-form-item label="机构名称:">
				    <el-input v-model="formInline.user" placeholder="请输入机构名称"></el-input>
				  </el-form-item>
				  <el-button type="warning" @click="SSinput">搜索</el-button>
				</el-form>
				<el-table :data="tableData" stripe style="width: 100%" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
					<el-table-column prop="cname" label="机构名称" align="center"></el-table-column>
					<el-table-column prop="ismember" label="是否是会员" align="center"></el-table-column>
					<el-table-column prop="re_num" label="总名额" align="center"></el-table-column>
					<el-table-column prop="surplus" label="充值名额" align="center"></el-table-column>
					<el-table-column prop="give_num" label="赠送名额" align="center"></el-table-column>
					<el-table-column label="操作" width="150"  align="center">
					  <template slot-scope="scope" >
						<el-button
						  size="mini"
						  type="success"
						  @click="handleEdit(scope.$index, scope.row)">设置赠送名额</el-button>
					  </template>
					</el-table-column>
				</el-table>
				<div class="block pagination">
				   <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
					:current-page.sync="form.page" :page-size="16" layout="prev, pager, next, jumper" :total="num">
					</el-pagination>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
	    data() {
	      return {
			tableData:[],
			minerCO:'',
			num:0,
			fenrunONCO:[],
	        formInline: {
	          user: '',
	          region: '',
	        },
			form:{
				page:1
			}
	      }
	    },
	    methods: {
		  // 分润设置
	      onSubmit() {
			let that =this;
			let data=that.fenrunONCO;
			that.$post('v1/setpcShareBenefit',data).then((response) => {
				that.$message({
					type: 'success',
					message: '操作成功!'
				});
			});
	      },
		  // 修改名额
		  onSubmit() {
			let that =this;
			let data=that.minerCO;
			that.$post('v1/setUserPrice',data).then((response) => {
				that.$message({
					type: 'success',
					message: '操作成功!'
				});
			});
		  },
		  //名额设置
		  onSubmittyk(){
			  
		  },
		  //搜索
		  SSinput(){
			  
		  },
		  //修改名额
		  handleEdit(index,row){
			  this.$prompt('请输入需要添加的赠送名额', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					inputPattern:'',
					inputType:'number',
					inputErrorMessage: '格式不正确'
				  }).then(({ value }) => {
					console.log(value)
				  }).catch(() => {
					this.$message({
					  type: 'info',
					  message: '取消输入'
					});       
				  });
			  },
		  // 分页
		  handleSizeChange(val) {
		  	this.form.page = val;
			 this.lietcon();
		  },
		  handleCurrentChange(val) {
		  	this.form.page = val;
			this.lietcon();
		  },
		  // 获取分润
		  fenrunOne(){
			  let that =this;
			  let data={};
			  that.$post('v1/getpcShareBenefit',data).then((response) => {
			  	that.fenrunONCO = response.data;
			  });
		  },
		  // 获取名额价格
		  miner(){
			  let that =this;
			  let data={};
			  that.$post('v1/getUserPrice',data).then((response) => {
				that.minerCO = response.data;
			  });
		  },
		  // 获取列表
		  lietcon(){
			  let that =this;
			  let data=that.form;
			  that.$post('v1/getpcRechargeList',data).then((response) => {
				  console.log(response.data);
				  if (response.code==1000) {
				  	response.data.info.forEach(obj=>{
				  		obj.ismember = that.statepay(obj.ismember);
				  	});
				  	that.tableData = response.data.info;
				  	that.num = response.data.num;
				  }else{
					  
				  }
			  });
		  },
		  //状态
		  statepay(e){
			  if (e==1) {
			  	e="会员"
			  } else{
			  	e="非会员"
			  };
			  return e
		  }
	    },
		mounted() {
			this.fenrunOne();
			this.miner();
			this.lietcon();
		}
	  }
</script>

<style scoped lang="less">
	@import '../../assets/style/index.less';//颜色样式
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
		}
		.InBoxBody-con{
			width:calc(100% - 80px);
			padding: 0 40px;
		}
	}
	.pagination{
		float: right;
		margin: 20px;
	}
</style>



