<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text">秒杀管理</span>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<span>秒杀结束时间设置</span>
				<el-input class="input" v-model="dataobj.name" :disabled="dataobj.determine_type==1?true:false" placeholder="秒杀主题"></el-input>
				<el-date-picker
				      v-model="dataobj.end_time"
				      type="datetime"
					  class="input"
					  :disabled="dataobj.determine_type==1?true:false"
				      placeholder="选择日期时间">
				</el-date-picker>
				<el-button class="time" type="warning" plain @click="scIN(1)" v-show="dataobj.determine_type==1?false:true">修 改</el-button>
				 <el-button class="time" type="warning" :disabled="dataobj.determine_type==1?true:false"  @click="scIN(2)">{{dataobj.determine_type==1?'活动已开启':'立即开启秒杀活动'}}</el-button>
			</div>
			<!-- 表单 -->
			 <el-table ref="multipleTable" :data="tableData" style="width: 100%" stripe tooltip-effect="dark" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
			    <el-table-column prop="cname" label="机构名称" align="center"></el-table-column>
				<el-table-column prop="cuname" label="秒杀课程名称" align="center"></el-table-column>
				<el-table-column prop="caname" label="课程分类" align="center"></el-table-column>
				<el-table-column prop="c_num" label="课时" align="center"></el-table-column>
				<el-table-column prop="present_price" label="秒杀价" align="center"></el-table-column>
				<el-table-column prop="original_price" label="原价" align="center"></el-table-column>
				<el-table-column prop="start_time" label="开课时间" align="center"></el-table-column>
				<el-table-column  label="是否会员" align="center">
					<template slot-scope="scope" >
						<p style="color:#67C23A;" v-if="scope.row.ismember==1">是</p>
						<p style="color:#F56C6C" v-if="scope.row.ismember==2">否</p>
					</template>
				</el-table-column>
				<el-table-column label="操作" width="220"  align="center">
				  <template slot-scope="scope" >
					<el-button
					  size="mini"
					  type="warning"
					  v-show="scope.row.examine_type==1?true:false"
					  @click="handleOK(2,scope.$index,scope.row)">同意</el-button>
					<el-button
					  size="mini"
					  v-show="scope.row.examine_type==1?true:false"
					  @click="handleOK(3,scope.$index,scope.row)">拒绝</el-button>
					<p style="color:#67C23A;" v-if="scope.row.examine_type==2">审核通过</p>
					<p style="color:#F56C6C" v-if="scope.row.examine_type==3">审核拒绝</p>
				  </template>
				</el-table-column>
			 </el-table>
			 <div class="block pagination">
			    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
				:current-page.sync="scinput.page" :page-size="16" layout="prev, pager, next, jumper" :total="num">
				</el-pagination>
			 </div>
		</div>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
		  valueTIME:'',
		  //总页数
		  num:1,
		  //获取的分类id
		  classid:null,
		  //提交内容
		  dataobj: {},
		  //搜索内容
		  scinput:{
			  name:'',
			  page:1
		  },
		  tableData: [],
	    }
	  },
	  methods: {
		//设置
		scIN(e){
			let url = 'v1/updatepcSeckillTheme',textname='修改成功',data={end_time:(this.dataobj.end_time).getTime(),name:this.dataobj.name}
			if(e==2){
				url = 'v1/setpcdetermineType',textname='开启成功',data={}
			}
			let that =this;
			that.$post(url,data).then((response) => {
				console.log('成功',response);
				if (response.code==1000) {
					that.$message({
						type: 'success',
						message: textname
					});
				};
			}); 
		},
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
			this.scinput.page = val;
			//机构列表渲染
			this.ObjAjax();
		},
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			this.scinput.page = val;
			//机构列表渲染
			this.ObjAjax();
		},
		//同意
		handleOK(state,index, row) {
			console.log(state,index, row);
			let textname='拒绝';
			if (state==2) {
				textname='同意'
			};
			this.$confirm('此操作将'+textname+'（'+row.cname+'）的秒杀申请, 是否继续?', '提示', {
			  confirmButtonText: '确定',
			  cancelButtonText: '取消',
			  type: 'warning'
			}).then(() => {
				let that =this;
				let data={
					seckillcourse_id:row.id,
					type:state
				};
				that.$post('v1/setpcExamineSeckillCourse',data).then((response) => {
					console.log('成功',response);
					if (response.code==1000) {
						that.$message({
							type: 'success',
							message: '成功!'
						});
						//机构列表渲染
						that.ObjAjax();
					};
				}); 
			}).catch(() => {
			  this.$message({
				type: 'info',
				message: '已取消'
			  });          
			});
		},
		//获取秒杀时间列表
		ObjAjax(){
			let that =this;
			let data=this.scinput;
			that.$post('v1/getpcSeckillCourseAll',data).then((response) => {
				console.log('成功',response);
				if (response.code==1000) {
					response.data.info.forEach(obj=>{
						obj.start_time = that.$timeZH(obj.start_time);						
					});
					this.tableData = response.data.info;
					this.num = response.data.num;
				};
				if (response.error_code==10000) {
					that.tableData=[];
				};
			}); 
		},
		// 秒杀设置
		ObjtimeAjax(){
			let that =this;
			that.$post('v1/getpcSeckillTheme').then((response) => {
				console.log('成功',response);
				if (response.code==1000) {
					this.dataobj = response.data
				};
			}); 
		},
	  },
	  mounted() {
		this.ObjtimeAjax();
	  	this.ObjAjax();
	  }
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
		}
		.InBoxBody-con{
			width: 100%;
			overflow: hidden;
			.InBoxBody-con-input{
				width: 100%;
				overflow: hidden;
				margin-bottom:20px;
				span{
				   margin-left: 20px;
				   color: @color4;
				   font-size: 16px;
				   float: left;
				   line-height: 32px;
				};
				.input{
					float: left;
					width: 200px;
					height: 32px;
					margin-left: 12px;
				};
				.time{
					float: left;
					height: 32px;
					margin-left: 12px;
				}
				.buttonRight{
					float: right;
					margin-right: 30px;
				}
			};
			.pagination{
				float: right;
				margin: 20px;
			}
			.logo{
				width: 50px;
				height: 50px;
				display: block;
				float: left;
				margin-top: 4px;
			}
			.logours{
				float: left;
				margin-left: 10px;
				width:80px;
				text-align: left;
				div{
					line-height: 60px;
				}
			}
		}
	}
	
	.avatar-uploader .el-upload {
	    border: 1px dashed #d9d9d9;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	  }
	  .avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	  }
	  .avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 120px;
	    height: 120px;
	    line-height: 120px;
	    text-align: center;
		border: 1px dashed #d9d9d9;
	  }
	  .avatar {
	    width: 120px;
	    height: 120px;
	    display: block;
	  }
	 .dialoginput{
		 width: 100%;
		 margin: 20px 0;
		 overflow: hidden;
		 .dialoginputFl{
			 width:calc(50% - 10px);
			 margin-right: 10px;
			 float: left;
		 }
	 }
</style>



