<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text">成长中心管理</span>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<span>成长中心搜索</span>
				<el-input class="input" placeholder="机构名称"></el-input>
				<el-select class="input" v-model="scinput.apply_type" clearable  placeholder="请选择状态">
				    <el-option
				      v-for="item in optionsnl"
				      :key="item.id"
				      :label="item.name"
				      :value="item.id">
				    </el-option>
				</el-select>
				<el-button class="time" type="warning" plain @click="scIN()">搜 索</el-button>
			</div>
			<!-- 表单 -->
			 <el-table ref="multipleTable" :data="tableData" style="width: 100%" stripe tooltip-effect="dark" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
			    <el-table-column fixed prop="cname" label="机构名称" align="center"></el-table-column>
				<el-table-column prop="cuname" label="课程名称" align="center"></el-table-column>
				<el-table-column prop="snname" label="成长中心名称" align="center"></el-table-column>
				<el-table-column prop="clname" label="成长中心教室" align="center"></el-table-column>
				<el-table-column prop="caname" label="课程分类" align="center"></el-table-column>
				<el-table-column prop="c_num" label="课时" align="center"></el-table-column>
				<el-table-column prop="present_price" label="总价" align="center"></el-table-column>
				<el-table-column prop="original_price" label="原价" align="center"></el-table-column>
				<el-table-column prop="start_time" label="开课时间" align="center"></el-table-column>
				<el-table-column  label="是否会员" align="center">
					<template slot-scope="scope" >
						<p style="color:#67C23A;" v-if="scope.row.ismember==1">是</p>
						<p style="color:#F56C6C" v-if="scope.row.ismember==2">否</p>
					</template>
				</el-table-column>
				<el-table-column label="操作" width="320" fixed="right" align="center">
				  <template slot-scope="scope" >
					<el-button
					    size="mini"
					    type="success"
						v-if="scope.row.apply_type==2"
					    @click="handledetails(scope.$index,scope.row)">单个时间修改</el-button>
					<el-button
					    size="mini"
					    type="primary"
						v-if="scope.row.apply_type==2"
					    @click="handleEdit(scope.$index,scope.row)">批量时间修改</el-button>
					<el-button
					  size="mini"
					  type="warning"
					  v-if="scope.row.apply_type==1"
					  @click="handleOK(2,scope.$index,scope.row)">同意</el-button>
					<el-button
					  size="mini"
					  v-if="scope.row.apply_type!=3"
					  @click="handleOK(3,scope.$index,scope.row)">拒绝</el-button>
					<p style="color:#F56C6C" v-if="scope.row.apply_type==3">审核拒绝</p>
				  </template>
				</el-table-column>
			 </el-table>
			 <div class="block pagination">
			    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
				:current-page.sync="scinput.page" :page-size="16" layout="prev, pager, next, jumper" :total="num">
				</el-pagination>
			 </div>
		</div>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
		  optionsnl:[
			  {id:'',name:'全部'},
			  {id:1,name:'待审核'},
			  {id:2,name:'审核通过'},
			  {id:3,name:'审核拒绝'},
		  ],
		  valueTIME:'',
		  //总页数
		  num:1,
		  //获取的分类id
		  classid:null,
		  //提交内容
		  dataobj: {},
		  //搜索内容
		  scinput:{
			  name:'',
			  apply_type:'',
			  page:1
		  },
		  tableData: [],
	    }
	  },
	  methods: {
		// 全部修改
		handleEdit(index, row){
			this.$storeState.HCommcourse = this.form;
			this.$router.push({path:'CompList/CompListCon',query:{mid:row.id}});
		},
		//单独修改
		handledetails(index, row) {
			this.$storeState.HCommcourse = this.form;
			this.$router.push({path:'CompList/CompListD',query:{mid:row.id}});
		},
		//搜索
		scIN(){
			this.ObjAjax();
		},
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
			this.scinput.page = val;
			//机构列表渲染
			this.ObjAjax();
		},
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			this.scinput.page = val;
			//机构列表渲染
			this.ObjAjax();
		},
		//同意
		handleOK(state,index, row) {
			console.log(state,index, row);
			let textname='拒绝';
			if (state==2) {
				textname='同意'
			};
			this.$confirm('此操作将'+textname+'（'+row.cname+'）的成长中心申请, 是否继续?', '提示', {
			  confirmButtonText: '确定',
			  cancelButtonText: '取消',
			  type: 'warning'
			}).then(() => {
				let that =this;
				let data={
					syntheticalscourse_id:row.id,
					type:state
				};
				that.$post('v1/setpcSyntheticalCourse',data).then((response) => {
					console.log('成功',response);
					if (response.code==1000) {
						that.$message({
							type: 'success',
							message: '成功!'
						});
						//机构列表渲染
						that.ObjAjax();
					};
				}); 
			}).catch(() => {
			  this.$message({
				type: 'info',
				message: '已取消'
			  });          
			});
		},
		//获取成长中心列表
		ObjAjax(){
			let that =this;
			let data=this.scinput;
			that.$post('v1/getpcSynthetical',data).then((response) => {
				console.log('成功',response);
				if (response.code==1000) {
					response.data.info.forEach(obj=>{
						obj.snname = that.moenystate(obj.snname);
						obj.clname = that.moenystate(obj.clname);
						if (obj.start_time==null) {
							obj.start_time = that.moenystate(obj.start_time);
						} else{
							obj.start_time = that.$timeZH(obj.start_time);
						}
					});
					this.tableData = response.data.info;
					this.num = response.data.num;
				};
				if (response.error_code==10000) {
					that.tableData=[];
				};
			}); 
		},
		// 为null为“-”
		moenystate(e){
			if(e==null){e='-'}
			return e
		},
	  },
	  mounted() {
	  	this.ObjAjax();
	  },
	  created(){
	  		//验证选中的状态是否存才
	  		if (this.$storeState.CompList!=null) {
	  			this.form = this.$storeState.CompList;
	  		};  
	  }
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
		}
		.InBoxBody-con{
			width: 100%;
			overflow: hidden;
			.InBoxBody-con-input{
				width: 100%;
				overflow: hidden;
				margin-bottom:20px;
				span{
				   margin-left: 20px;
				   color: @color4;
				   font-size: 16px;
				   float: left;
				   line-height: 32px;
				};
				.input{
					float: left;
					width: 200px;
					height: 32px;
					margin-left: 12px;
				};
				.time{
					float: left;
					height: 32px;
					margin-left: 12px;
				}
				.buttonRight{
					float: right;
					margin-right: 30px;
				}
			};
			.pagination{
				float: right;
				margin: 20px;
			}
			.logo{
				width: 50px;
				height: 50px;
				display: block;
				float: left;
				margin-top: 4px;
			}
			.logours{
				float: left;
				margin-left: 10px;
				width:80px;
				text-align: left;
				div{
					line-height: 60px;
				}
			}
		}
	}
	
	.avatar-uploader .el-upload {
	    border: 1px dashed #d9d9d9;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	  }
	  .avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	  }
	  .avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 120px;
	    height: 120px;
	    line-height: 120px;
	    text-align: center;
		border: 1px dashed #d9d9d9;
	  }
	  .avatar {
	    width: 120px;
	    height: 120px;
	    display: block;
	  }
	 .dialoginput{
		 width: 100%;
		 margin: 20px 0;
		 overflow: hidden;
		 .dialoginputFl{
			 width:calc(50% - 10px);
			 margin-right: 10px;
			 float: left;
		 }
	 }
</style>



