<template>
	<div class="homerightcon">
		<!-- 招生营销 -->
		<div class="homerightcon-one">
			<div class="homerightcon-one-header">
				<span class="homerightcon-one-header-text">收入趋势</span>
				<span class="homerightcon-one-header-more"><i class="el-icon-more"></i></span>
			</div>
			<div class="homerightcon-one-con">
				<div id="mainse" style="width:100%;height:160px;margin-bottom:24px;"></div>
			</div>
		</div>
		<!-- 资金趋势 -->
		<div class="homerightcon-two">
			<div class="homerightcon-two-header">
				<span class="homerightcon-two-header-text">资金趋势</span>
			</div>
			<div class="homerightcon-two-con">
				<div id="mainthree" style="width:100%;height:160px;margin-bottom:24px;"></div>
			</div>
		</div>
		<!-- 机构排行分析 -->
		<div class="homerightcon-two">
			<div class="homerightcon-two-header">
				<span class="homerightcon-two-header-text">机构排行分析</span>
			</div>
			<div class="homerightcon-two-con">
				<div id="mainFour" style="width:100%;height:160px;margin-bottom:40px;"></div>
			</div>
		</div>
	</div>
</template>

<script>
	var echarts = require('echarts');
	export default {
		data() {
			return {
				echartslist:{time:['5月', '6月', '7月', '8月', '9月', '10月', '11月'],data:[10, 52, 200, 334, 390, 330, 220]}
			};
		},
		methods: {
			echartsTB() {
				let that = this;
				// 基于准备好的dom，初始化echarts实例
				var myChart = echarts.init(document.getElementById('mainse'));
				// 绘制图表
				myChart.setOption({
					tooltip: {
						trigger: 'axis',
						axisPointer: {
							type: 'cross',
							label: {
								backgroundColor: '#6a7985'
							}
						}
					},
					grid: {
						left: '-2%',
						right: '2%',
						bottom: '0%',
						top: '4%',
						containLabel: true
					},
					xAxis: [{
						type: 'category',
						boundaryGap: false,
						data: ['01', '02', '03', '04', '05', '06', '07']
					}],
					yAxis: [{
						type: 'value',
						show: false,
					}],
					series: [{
							name: '本月结算',
							type: 'line',
							stack: '总量',
							areaStyle: {},
							smooth: true,
							data: [120, 132, 101, 134, 90, 230, 210],
							itemStyle: {
								normal: {
									color: '#f7c367'
								}
							},
							areaStyle: {
								normal: {
									color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
										offset: 0,
										color: '#f7c367'
									}, {
										offset: 1,
										color: 'rgba(255,255,255,0.5)'
									}])
								}
							},
						},
						{
							name: '本月退款',
							type: 'line',
							stack: '总量',
							areaStyle: {},
							smooth: true,
							data: [220, 182, 191, 234, 290, 330, 310],
							itemStyle: {
								normal: {
									color: '#fa678a'
								}
							},
							areaStyle: {
								normal: {
									color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
										offset: 0,
										color: '#fa678a'
									}, {
										offset: 1,
										color: 'rgba(255,255,255,0.5)'
									}])
								}
							},
						}
					]
				});
			},
			echartsthree() {
				let that = this;
				// 基于准备好的dom，初始化echarts实例
				var myChart = echarts.init(document.getElementById('mainthree'));
				// 绘制图表
				myChart.setOption({
					color: ['#3398DB'],
					tooltip: {
						trigger: 'axis',
						axisPointer: { // 坐标轴指示器，坐标轴触发有效
							type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
						}
					},
					grid: {
						left: '3%',
						right: '4%',
						bottom: '3%',
						top: '6%',
						containLabel: true
					},
					xAxis: [{
						type: 'category',
						data: that.echartslist.time,
						axisTick: {
							alignWithLabel: true
						}
					}],
					yAxis: [{
						type: 'value',
						axisLine: {
							show: false
						}
					}],
					series: [{
						name: '订单数据',
						type: 'bar',
						barWidth: '60%',
						data: that.echartslist.data,
						barWidth: 18, //柱图宽度
						itemStyle: { //柱图渐变颜色
							normal: {
								color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
									offset: 1,
									color: "#51def9" // 0% 处的颜色
								}, {
									offset: 0,
									color: "#7dbff3" // 100% 处的颜色
								}], false),
								barBorderRadius: [5, 5, 0, 0]
							}
						},
					}]
				});
			},
			echartsFour(){
						let that =this;
						  // 基于准备好的dom，初始化echarts实例
						  var myChart = echarts.init(document.getElementById('mainFour'));
						  // 绘制图表
						  myChart.setOption({
							tooltip : {
							        trigger: 'item',
							        formatter: "{b} : {c} ({d}%)"
							    },
							    series : [
							        {
							            type: 'pie',
							            radius : '80%',
							            center: ['50%', '50%'],
							            selectedMode: 'single',
							            data:[
							                {value:1548,name: 'XX机构'},
							                {value:535, name: 'XX机构'},
							                {value:510, name: 'XX机构'},
							                {value:634, name: 'XX机构'},
							                {value:735, name: 'XX机构'}
							            ],
										roseType: 'radius',
							            itemStyle: {
							                emphasis: {
							                    shadowBlur: 10,
							                    shadowOffsetX: 0,
							                    shadowColor: 'rgba(0, 0, 0, 0.5)'
							                },
											normal:{
												color:function(params) {
												//自定义颜色
												var colorList = [          
														'#f2d182', '#f7b84f', '#f38382', '#fef260', '#f7c367', '#fa678a',
													];
													return colorList[params.dataIndex]
												 }
											}
							            }
							        }
							    ]
						  }); 
			}
		},
		mounted() {
			// 第一个图表
			this.echartsTB();
			// 第二个图表
			this.echartsthree();
			//第三个饼图
			this.echartsFour();
		},
	}
</script>

<style scoped lang="less">
	@import '../../assets/style/index.less'; //颜色样式

	.homerightcon {
		width: 100%;
		float: left;

		.homerightcon-one {
			width: 100%;
			background: @bgcolor;
			border: 1px solid @bordersolid;
			border-radius: 10px;

			.homerightcon-one-header {
				width: calc(100% - 40px);
				padding: 20px 20px;

				.homerightcon-one-header-text {
					padding-left: 6px;
					font-size: 16px;
					border-left: @borderliftsolid;
				}

				.homerightcon-one-header-more {
					float: right;
					color: @color4;
					font-size: 16px;
					cursor: pointer;

					i {
						font-size: 20px;
					}
				}
			}

			.homerightcon-one-con {
				width: 100%;
				text-align: center;
				color: #FFFFFF;
				margin-bottom: 27px;

				.homerightcon-one-con-icon {
					background-color: @color2;
					border-radius: 8px;
					padding: 24px 0;
					cursor: pointer;

					i {
						font-size: 30px;
						margin-bottom: 6px;
					}
				}

				.homerightcon-one-con-box {
					border: 1px #E3E3E5 solid;
					border-radius: 8px;
					padding: 23px 0;
					color: #C0C0C0;
					cursor: pointer;

					i {
						font-size: 30px;
						margin-bottom: 6px;
					}
				}
			}
		}

		// 平台服务
		.homerightcon-two {
			width: 100%;
			background: @bgcolor;
			border: 1px solid @bordersolid;
			border-radius: 10px;
			margin-top: 30px;
			overflow: hidden;

			.homerightcon-two-header {
				width: calc(100% - 40px);
				padding: 20px 20px;

				.homerightcon-two-header-text {
					padding-left: 6px;
					font-size: 16px;
					border-left: @borderliftsolid;
				}
			}

			.homerightcon-two-con {
				width: 100%;
			}
		}
	}

	.homerightcon-three {
		width: 100%;
		
	}









	.el-carousel__item h3 {
		color: #475669;
		font-size: 18px;
		opacity: 0.75;
		line-height: 240px;
		margin: 0;
		text-align: center;
	}

	.el-carousel__item:nth-child(2n) {
		background-color: #99a9bf;
	}

	.el-carousel__item:nth-child(2n+1) {
		background-color: #d3dce6;
	}
</style>



