<template>
	<div style="overflow: hidden;">
		<el-table :data="tableData" style="width: 100%" :default-sort="{prop: 'date', order: 'descending'}">
			<el-table-column prop="Ranking" align="center" label="排行">
			</el-table-column>
			<el-table-column prop="name" align="center" label="机构名称">
			</el-table-column>
			<el-table-column prop="date" label="订单" align="center" sortable width="180">
			</el-table-column>
			<el-table-column prop="datesr" label="订单收入" align="center" sortable width="180">
			</el-table-column>
			<el-table-column prop="datesr" label="平台收入" align="center" sortable width="180">
			</el-table-column>
			<el-table-column prop="datesr" label="退款支出" align="center" sortable width="180">
			</el-table-column>
			<el-table-column prop="datesr" label="会员数量" align="center" sortable width="180">
			</el-table-column>
		</el-table>
		<el-pagination style="float: right;margin: 30px 30px 0 0;" @size-change="handleSizeChange" @current-change="handleCurrentChange"
		 :current-page.sync="currentPage" :page-size="100" layout="prev, pager, next, jumper" :total="1000">
		</el-pagination>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				tableData: [{
						Ranking: '1',
						date: '12',
						datesr: '8000',
						name: 'XX机构',
						address: '上海市普陀区金沙江路 1518 弄',
					},
					{
						Ranking: '1',
						date: '12',
						datesr: '8000',
						name: 'XX机构',
						address: '上海市普陀区金沙江路 1518 弄',
					},
					{
						Ranking: '1',
						date: '12',
						datesr: '8000',
						name: 'XX机构',
						address: '上海市普陀区金沙江路 1518 弄',
					},
					{
						Ranking: '1',
						date: '12',
						datesr: '8000',
						name: 'XX机构',
						address: '上海市普陀区金沙江路 1518 弄',
					}
				],
				currentPage: 1
			}
		},
		methods: {
			formatter(row, column) {
				return row.address;
			},
			handleSizeChange(val) {
				console.log(`每页 ${val} 条`);
			},
			handleCurrentChange(val) {
				console.log(`当前页: ${val}`);
			}
		}
	}
</script>



