<template>
	<div class="liftnav" :style="'width:'+navwidth+'px'">
		<div class="liftnavcon">
			<div class="liftnav-headerimg" v-show="isCollapse==false?true:false">
				<img src="../../assets/images/logo.png" alt="">
			</div>
			<div class="liftnav-headerimgx" v-show="isCollapse==false?false:true">
				<img src="../../assets/images/logo.png" alt="">
			</div>
			<div class="liftnavconlist">
				<el-menu router :default-active="$route.path" unique-opened class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse" :collapse-transition="navames">
				  <!-- 导航 -->
				  <!-- 无下拉 -->
				  <el-menu-item index="/index" >
					  <i class="el-icon-house"></i>
					  <span slot="title">首页</span>
				  </el-menu-item>
				 <!-- 数据统计 -->
				  <el-submenu index="2">
					<template slot="title">
					  <i class="el-icon-data-analysis"></i>
					  <span slot="title">数据统计</span>
					</template>
					<el-menu-item index="/UserAn">用户分析</el-menu-item>
					<el-menu-item index="/Menchlist">机构分析</el-menu-item>
					<el-menu-item index="/MapFb">数据分布</el-menu-item>
				  </el-submenu>
				  <!-- 机构管理 -->
				   <el-submenu index="3">
					<template slot="title">
					  <i class="el-icon-school"></i>
					  <span slot="title">机构管理</span>
					</template>
					<el-menu-item index="/InstList">机构列表</el-menu-item>
				   </el-submenu>
				   <!-- 用户管理 -->
					<el-submenu index="4">
						<template slot="title">
						  <i class="el-icon-user"></i>
						  <span slot="title">用户管理</span>
						</template>
						<el-menu-item index="/UserList">用户列表</el-menu-item>
						<!-- <el-menu-item index="/UserEval">评价列表</el-menu-item> -->
						<!-- <el-menu-item index="/TeacReal">教学实况</el-menu-item> -->
					</el-submenu>
				   <!-- 成长中心管理 -->
					<el-submenu index="5">
						<template slot="title">
						  <i class="el-icon-office-building"></i>
						  <span slot="title">成长中心</span>
						</template>
						<!-- <el-menu-item index="/CompList">成长中心申请列表</el-menu-item> -->
						<el-menu-item index="/ComplexJG">成长中心列表</el-menu-item>
						<!-- <el-menu-item index="/Complexroom">成长中心教室管理</el-menu-item> -->
					</el-submenu>
					<!-- 分类管理 -->
					<el-submenu index="6">
						<template slot="title">
						  <i class="el-icon-files"></i>
						  <span slot="title">分类管理</span>
						</template>
						<el-menu-item index="/ClassificCou">课程分类</el-menu-item>
						<el-menu-item index="/ClassificClaR">教室分类</el-menu-item>
						<el-menu-item index="/ClassificTR">教师分类</el-menu-item>
						<el-menu-item index="/ClassificCom">能力技能分类</el-menu-item>
					</el-submenu>
					<!-- 营销管理 -->
					<el-submenu index="7">
						<template slot="title">
						  <i class="el-icon-c-scale-to-original"></i>
						  <span slot="title">营销管理</span>
						</template>
						<el-menu-item index="/GMSecondkill">秒杀活动管理</el-menu-item>
					</el-submenu>
					<!-- 社区管理 -->
					<el-submenu index="8">
						<template slot="title">
						  <i class="el-icon-news"></i>
						  <span slot="title">社区管理</span>
						</template>
						<el-menu-item index="/HComm">社区列表</el-menu-item>
						<el-menu-item index="/HCommroom">社区教室</el-menu-item>
						<el-menu-item index="/HCommclass">社区课目</el-menu-item>
						<el-menu-item index="/HCommcourse">社区课程</el-menu-item>
						<!-- <el-menu-item index="/HCommtea">社区老师</el-menu-item> -->
					</el-submenu>
					<!-- 平台管理 -->
					<el-submenu index="9">
						<template slot="title">
						  <i class="el-icon-cpu"></i>
						  <span slot="title">平台管理</span>
						</template>
						<el-menu-item index="/CarouImg">轮播图管理</el-menu-item>
						<el-menu-item index="/IPBanner">广告图管理</el-menu-item>
						<el-menu-item index="/NewRecomK">课程热门推荐</el-menu-item>
						<el-menu-item index="/NewRecomJ">课程机构推荐</el-menu-item>
					</el-submenu>
					<!-- 订单管理 -->
					<el-submenu index="10">
						<template slot="title">
						  <i class="el-icon-document-copy"></i>
						  <span slot="title">订单管理</span>
						</template>
						<el-menu-item index="/orderUnpaid">已付款订单</el-menu-item>
						<el-menu-item index="/orderConduct">进行中订单</el-menu-item>
						<el-menu-item index="/orderlesson">体验课订单</el-menu-item>
						<el-menu-item index="/orderrefund">退款订单</el-menu-item>
						<el-menu-item index="/orderComm">社区活动课订单</el-menu-item>
						<el-menu-item index="/orderHistory">历史订单</el-menu-item>
					</el-submenu>
					<!-- 财务管理 -->
					<el-submenu index="11">
						<template slot="title">
						  <i class="el-icon-money"></i>
						  <span slot="title">财务管理</span>
						</template>
						<el-menu-item index="/FinaAnal">平台财务分析</el-menu-item>
						<el-menu-item index="/BudgetData">财务列表</el-menu-item>
						<el-menu-item index="/MenchCashList">机构提现表</el-menu-item>
						<el-menu-item index="/UserConsDeta">财务设置</el-menu-item>
					</el-submenu>
					<!-- 系统设置 -->
					<el-submenu index="12">
						<template slot="title">
						  <i class="el-icon-setting"></i>
						  <span slot="title">系统设置</span>
						</template>
						<el-menu-item index="/HelpCenter">帮助中心设置</el-menu-item>
						<!-- <el-menu-item index="/PrivMana">权限管理</el-menu-item> -->
						<el-menu-item index="/SystSetup">系统设置</el-menu-item>
					</el-submenu>
				</el-menu>
			</div>
		</div>
		<!-- 收缩块 -->
		<div class="liftnav-button" @click="navHs()"></div>
	</div>
</template>

<script>
	export default {
	    data() {
	      return {
			thisroute:'/',
	        isCollapse: false,
			navames:false,
			navwidth:200,
			datalist:[]
	      };
	    },
	    methods: {
	      handleOpen(key, keyPath) {
	        console.log(key, keyPath);
	      },
	      handleClose(key, keyPath) {
	        console.log(key, keyPath);
	      },
		  //导航回收展开
		  navHs(){
			if (this.isCollapse===false) {
				this.isCollapse=true;
				this.navwidth = 64;
				this.$emit('ConHei','calc(100% - 64px)');
			} else{
				this.isCollapse=false;
				this.navwidth = 200;
				this.$emit('ConHei','calc(100% - 200px)');
			};
		  }
	    },
		mounted() {
			// console.log(this.$route.path)
		},
	  }
</script>

<style scoped lang="less">
	.el-menu{
		border:none;
		background-color:inherit;
	}
	.el-menu-vertical-demo:not(.el-menu--collapse) {
	    width: 200px;
	    min-height: 400px;
	  }
	.el-menu-item.is-active{
		color:@color2;
		background-color:@nackcolor;
	}
	@import '../../assets/style/index.less';//颜色样式
	.liftnav{
		height: 100%;
		float: left;
		background-color: @navbackcolor;
		box-shadow:@navboxShadow;
		position: relative;
		.liftnavcon{
		  width: 200px;
		  height:100%;
		  overflow: hidden;
		  .liftnavconlist{
			  width: 200px;
			  height: calc(100% - 120px);
			  overflow-x: hidden;
			  overflow-y: auto;
			  padding-right: 15px;
		  }
		}
		.liftnav-button{
			position: absolute;
			width: 14px;
			height: 80px;
			background-color: @color2;
			top:50%;
			right: -14px;
			margin-top: -40px;
			border-top-right-radius: 80px;
			border-bottom-right-radius: 80px;
		}
		.liftnav-headerimg{
			width: 80px;
			height: 80px;
			background-color:@color3;
			border-radius: 50%;
			margin: 20px auto;
			img{
				display: block;
				width: 60px;
				height: 60px;
				border-radius: 50%;
				overflow: hidden;
				float: left;
				margin: 10px;
			}
		}
		.liftnav-headerimgx{
			width: 64px;
			height: 64px;
			img{
				width: 40px;
				height: 40px;
				border-radius: 50%;
				overflow: hidden;
				display: block;
				float: left;
				margin: 12px;
			}
		}
	}
	.liftnavlogo{
		background-color: @color1;
		widows: 100%;
		height:70px;
		img{
			width:50px;
			display: block;
			margin: 0 auto;
			padding-top: 10px;
		}
	}
</style>



