<template>
  <div class="indexbody">
	  <!-- 左导航 -->
	  <Liftnav v-on:ConHei="ConHei"></Liftnav>
	  <!-- 内容模块 -->
	  <div class="indexbody-content" :style="'width:'+indexbodyContentHeight">
		  <!-- 头部 -->
		 <Topnav></Topnav> 
		 <div class="indexbody-content-con">
			<router-view />
		 </div>
	  </div>      
  </div>
</template>

<script>
// import {mapState,mapGetters,mapActions} from 'vuex';
import Liftnav from "./nav/liftnav.vue"; //列表组件
import Topnav from "./nav/topnav.vue"; //头部组件
export default {
  components:{
	  Liftnav,
	  Topnav
  },
  data () {
    return {
      indexbodyContentHeight:'calc(100% - 200px)',//默认内容宽度
    }
  },
  methods:{
	  //内容板块的宽度
	  ConHei(e){
		  this.indexbodyContentHeight = e;
	  },
	  //提示内容
	  open() {
	  	// this.$notify({
	  	//   title: '提示内容',
	  	//   dangerouslyUseHTMLString: true,
	  	//   message: '<strong>这是 <i>HTML</i> 提示内容</strong>',
	  	//   duration: 0
	  	// });
	  }
  },
  mounted() {
  	this.open();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.indexbody{
		width: 100%;
		height: 100%;
	}
	.indexbody-content{
		float: left;
		height: 100%;
	}
	.indexbody-content-con{
		width: calc(100% - 60px);
		height: calc(100% - 100px);
		padding: 30px 30px 0 30px;
		overflow-x: hidden;
		overflow-y: auto;
	}
</style>



