<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
			  <el-breadcrumb-item :to="{ path: '/InstList' }">机构列表</el-breadcrumb-item>
			  <el-breadcrumb-item>机构详情</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con">
			<el-divider content-position="left">基本信息</el-divider>
			<div class="InBoxBody-con-two">
				<div class="InBoxBody-con-two-float">
					<div class="boxfloat">
						<div>机构名称：<span>{{intsinof.cname}}</span></div>
						<div>联系人：<span>{{intsinof.nickname}}</span></div>
					</div>
					<div class="boxfloat">
						<div>联系电话：<span>{{intsinof.phone}}</span></div>
						<div>账号：<span>{{intsinof.username}}</span></div>
					</div>	
				</div>
				<div class="InBoxBody-con-two-float">
					<div class="boxfloat">
						<div>注册时间：<span>{{intsinof.create_time}}</span></div>
					</div>
					<div class="boxfloat">
						<div>累计获得金额：<span>{{intsinof.cumulative_price}}</span></div>
						<div>累计退款金额：<span>{{intsinof.cumulative_retreat_price}}</span></div>
					</div>	
				</div>
			</div>
			<el-divider content-position="left">机构信息</el-divider>
			<!-- 机构信息 -->
			<div class="InBoxBody-con-two">
				<div class="InBoxBody-con-two-float" style="width: 100%;">
					<div class="boxfloat">
						<div>机构名称：<span>{{intsinof.cname}}</span></div>
						<div>机构地址：<span>{{intsinof.province+'/'+intsinof.city+'/'+intsinof.area}}</span></div>
					</div>
					<div class="boxfloat">
						<div>详细地址：<span>{{intsinof.province+intsinof.city+intsinof.area+intsinof.address}}</span></div>
						<div>联系人：<span>{{intsinof.nickname}}</span></div>
					</div>
					<div class="boxfloat">
						<div>联系电话：<span>{{intsinof.phone}}</span></div>
						<div>营业执照编号：<span>{{intsinof.organization}}</span></div>
					</div>
					<div class="boxfloat" style="width: 100%;">
						<div style="width: 100%;">
							<div style="width: 100%;">上传营业执照或者其他有效证书：</div>
							<el-image 
							    style="width: 200px; height: 200px;float: left;margin: 0.625rem;"
								v-for = "(item,index) in intsinof.mlicense" :key="index"
								:src="item" 
							    :preview-src-list="intsinof.mlicense">
							 </el-image>
						</div>
					</div>
					<div class="boxfloat">
						<div>机构简介：<span>{{intsinof.remarks}}</span></div>
					</div>	
				</div>
			</div>
			<el-divider content-position="left">机构管理</el-divider>
			<div class="InBoxBody-con-two" style="padding-bottom: 40px;">
				<el-button @click="Classroom()">教室管理</el-button>
				<el-button @click="TeacherAdmin()">教师管理</el-button>
				<el-button @click="ClassesAdmin()">课目管理</el-button>
				<el-button @click="coursemanagement()">课程管理</el-button>
				<el-button @click="Experience()">体验课管理</el-button>
				<el-button @click="Secondkill()">秒杀课管理</el-button>
		    </div>
		</div>
	</div>
</template>

<script>
	 export default {
	    data() {
	      return {
			  intsinof:[]
	      }
	    },
		methods:{
			//教室管理
			Classroom(){
				this.$router.push({path:'/InstList/instdetl/Classroom',query:{id:this.$route.query.id}});
			},
			//教师管理
			TeacherAdmin(){
				this.$router.push({path:'/InstList/instdetl/TeacherAdmin',query:{id:this.$route.query.id}});
			},
			ClassesAdmin(){
				this.$router.push({path:'/InstList/instdetl/ClassesAdmin',query:{id:this.$route.query.id}});
			},
			coursemanagement(){
				this.$router.push({path:'/InstList/instdetl/coursemanagement',query:{id:this.$route.query.id}});
			},
			Experience(){
				this.$router.push({path:'/InstList/instdetl/Experience',query:{id:this.$route.query.id}});
			},
			Secondkill(){
				this.$router.push({path:'/InstList/instdetl/Secondkill',query:{id:this.$route.query.id}});
			},
		},
		mounted() {
			//详情
			let data={
			  mem_id:this.$route.query.id
			};
			this.$post('v1/getzhtMemberdetails',data).then((response) => {
				console.log('成功',response);
				if (response.code==1000) {
					this.intsinof = response.data
				};
			});
		}
	  }
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
		}
		.InBoxBody-con{
			width: 100%;
			.InBoxBody-con-one{
				width: calc(100% - 40px);
				overflow: hidden;
				padding:0 20px 20px 20px;
				img{
					width: 80px;
					height: 80px;
					border-radius: 50%;
					overflow: hidden;
					display: block;
					float: left;
				};
				.InBoxBody-con-name{
					float: left;
					margin-left: 20px;
					font-size: 16px;
					color: @color5;
					padding: 10px 0;
					line-height: 30px;
				}
			};
			.InBoxBody-con-two{
				padding: 20px;
				font-size: 14px;
				overflow: hidden;
				.InBoxBody-con-two-float{
					width: 50%;
					float: left;
					overflow: hidden;
					p{
						color: @color4;
					}
					.boxfloat{
						width: 100%;
						line-height:40px;
						float: left;
						overflow: hidden;
						margin-bottom: 6px;
						div{
							width: 50%;
							float: left;
							span{
								color:@color4;
							}
							i{
								font-style: normal;
								color: @color2;
								margin-left: 12px;
							}
						}
						.boxfloatimg{
							width: 200px;
							height: 200px;
							display:block;
							float: left;
							margin: 0.625rem;
						}
					}
				}				
			}
			.InBoxBody-con-list{
				width: 100%;
				padding: 0 0 40px 0;
				p{
					color: @color4;
					margin-left: 20px;
					font-size: 14px;
				}
				.pagination{
					float: right;
					margin: 20px;
				}
			}
		}
	}
</style>



