<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text">机构列表</span>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<span>信息搜索</span>
				<el-form ref="form" :model="form" label-width="80px">
					<el-input class="input" v-model="form.cname" placeholder="机构名称"></el-input>
					<div class="block time">
					  <el-date-picker
						 v-model="form.time"
						  type="daterange"
						  align="right"
						  value-format="timestamp"
						  unlink-panels
						  range-separator="至"
						  start-placeholder="开始日期"
						  end-placeholder="结束日期"
						  :picker-options="pickerOptions">
					  </el-date-picker>
					</div>
					<el-select style="float: left;margin-left: 12px;" clearable v-model="form.is_verification" placeholder="请选择机构状态">
					  <el-option label="新注册机构" value="2"></el-option>
					  <el-option label="待审核" value="3"></el-option>
					  <el-option label="审核拒绝" value="4"></el-option>
					  <el-option label="审核通过" value="1"></el-option>
					</el-select>
					<el-button class="time" type="warning" plain @click="Search">搜   索</el-button>
				</el-form>
			</div>
			<!-- 表单 -->
			 <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark"  style="width: 100%" stripe  
			 @selection-change="handleSelectionChange" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
			    <el-table-column type="selection" width="55"></el-table-column>
				<el-table-column prop="uid" label="机构编号" align="center"></el-table-column>
				<el-table-column prop="cname" label="机构名称" align="center"></el-table-column>
				<el-table-column prop="nickname" label="联系人" align="center"></el-table-column>
				<el-table-column prop="create_time" label="注册时间" align="center"></el-table-column>
				<el-table-column prop="phone" label="联系电话" align="center"></el-table-column>
				<el-table-column prop="is_verification" label="状态" align="center"></el-table-column>
				<el-table-column prop="cumulative_price" label="累计获得金额" align="center"></el-table-column>
			    <el-table-column prop="cumulative_retreat_price" label="累计退款金额"  align="center"></el-table-column>
				<el-table-column label="操作" width="250"  align="center">
				  <template slot-scope="scope" >
					<el-button
					  size="mini"
					  type="success"
					  v-if="scope.row.is_verification=='待审核'"
					  @click="handleEdit(scope.$index, scope.row,'1')">通过</el-button>
					<el-button
						size="mini"
						type="danger"
						v-if="scope.row.is_verification=='待审核'"
						@click="handleEdit(scope.$index, scope.row,'2')">拒绝</el-button>
					<el-button
					  size="mini"
					  type="warning"
					  @click="handleDelete(scope.$index, scope.row)">详情</el-button>
				  </template>
				</el-table-column>
			 </el-table>
			 <div class="block pagination">
			    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
				:current-page.sync="form.page" :page-size="16" layout="prev, pager, next, jumper" :total="num">
				</el-pagination>
			 </div>
		</div>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
		  num:0,//总页数
		  form:{
			cname:'',
			start_time:'',
			is_verification:'3',
			page:1
		  },
		  textarea: '',
		  dialogVisible: false,
		  pickerOptions: {
			shortcuts: [{
			  text: '最近一周',
			  onClick(picker) {
				const end = new Date();
				const start = new Date();
				start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
				picker.$emit('pick', [start, end]);
			  }
			}, {
			  text: '最近一个月',
			  onClick(picker) {
				const end = new Date();
				const start = new Date();
				start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
				picker.$emit('pick', [start, end]);
			  }
			}, {
			  text: '最近三个月',
			  onClick(picker) {
				const end = new Date();
				const start = new Date();
				start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
				picker.$emit('pick', [start, end]);
			  }
			}]
		  },
		  tableData: [],
		  multipleSelection: []
	    }
	  },
	  methods: {
		//搜索内容
		Search(){
			this.form.page=1;
			this.ObjList();
		},
		toggleSelection(rows) {
		  if (rows) {
			rows.forEach(row => {
			  this.$refs.multipleTable.toggleRowSelection(row);
			});
		  } else {
			this.$refs.multipleTable.clearSelection();
		  }
		},
		handleSelectionChange(val) {
		  this.multipleSelection = val;
		},
		// 分页
		handleSizeChange(val) {
			this.form.page = val;
			this.ObjList();
		},
		handleCurrentChange(val) {
			this.form.page = val;
			this.ObjList();
		},
		//通过拒绝
		handleEdit(index, row,e) {
			console.log(index, row,e);
			let msg = '',statetyp='',that =this
			if (e==1) {
				msg = '是否通过（'+row.cname+'）的信息审核';
				statetyp = '1';
			} else{
				msg = '是否拒绝（'+row.cname+'）的信息审核';
				statetyp = '4';
			}
			this.$confirm(msg, '提示', {
			  confirmButtonText: '确定',
			  cancelButtonText: '取消',
			  type: 'warning'
			}).then(() => {
			  let data={
				  is_verification:statetyp,
				  mem_id:row.uid
			  };
			  that.$post('v1/updateMemberVerification',data).then((response) => {
			  	console.log('成功',response);
			  	that.$message({
					type: 'success',
					message: '操作成功!'
			  	});
				that.ObjList();
			  });
			}).catch(() => {
			  this.$message({
				type: 'info',
				message: '已取消'
			  });          
			});
		},
		//详情
		handleDelete(index, row) {
			console.log(index, row);
			//缓存状态传入
			this.$storeState.InstListNav = this.form;
			this.$router.push({path:'/InstList/instdetl',query:{id:row.uid}});
		},
		handleClose(done) {
		this.$confirm('确认关闭？')
		  .then(_ => {
			done();
		  })
		  .catch(_ => {});
		},
		//渲染数据
		ObjList(){
			let that =this;
			let data=this.form;
			that.$post('v1/getzhtMemberlist',data).then((response) => {
				console.log('成功',response);
				if (response.code==1000) {
					response.data.info.forEach(obj=>{
						obj.create_time = that.$timeZH(obj.create_time);
						obj.is_verification = that.statepay(obj.is_verification);
						obj.cumulative_price = that.moenystate(obj.cumulative_price);
						obj.cumulative_retreat_price = that.moenystate(obj.cumulative_retreat_price);
					});
					that.tableData = response.data.info;
					that.num = response.data.num;
				};
				if (response.error_code==10000) {
					that.tableData=[]
				};
			});
		},
		// 状态转换
		statepay(e){
			let namer = '';
			if (e==1) {
				namer = '审核通过'
			} else if(e==2){
				namer = '新注册机构'
			} else if(e==3){
				namer = '待审核'
			} else if(e==4){
				namer = '审核拒绝'
			};
			return namer
		},
		// 金额为null为“-”
		moenystate(e){
			if(e==null){e='-'}
			return e
		}
	  },
	  mounted() {
	  	this.ObjList();
	  },
	  created(){
		//验证选中的状态是否存才
		if (this.$storeState.InstListNav!=null) {
			this.form = this.$storeState.InstListNav;
		};  
	  }
	}
</script>

<style scoped lang="less">
	@import '../../assets/style/index.less';//颜色样式
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
		}
		.InBoxBody-con{
			width: 100%;
			overflow: hidden;
			.InBoxBody-con-input{
				width: 100%;
				overflow: hidden;
				margin-bottom:20px;
				span{
				   margin-left: 20px;
				   color: @color4;
				   font-size: 16px;
				   float: left;
				   line-height: 32px;
				};
				.input{
					float: left;
					width: 200px;
					height: 32px;
					margin-left: 12px;
				};
				.time{
					float: left;
					height: 32px;
					margin-left: 12px;
				}
				.buttonRight{
					float: right;
					margin-right: 30px;
				}
			};
			.pagination{
				float: right;
				margin: 20px;
			}
			.logo{
				width: 50px;
				height: 50px;
				display: block;
				margin: 0 auto;
			}
		}
	}
</style>



