<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text">轮播图管理</span>
			<el-button class="buttonRight" round @click="handleEdit('1')">新 增</el-button>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<span>轮播图</span>
			</div>
			<!-- 表单 -->
			 <el-table ref="multipleTable" :data="tableData" style="width: 100%" stripe tooltip-effect="dark" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
			    <el-table-column prop="sort" label="排序" align="center"></el-table-column>
				<el-table-column label="轮播图" align="center" width="160">
				  <template slot-scope="scope" style="overflow: hidden;">
					  <img class="logo" :src="scope.row.imges" alt="">
				  </template>
				</el-table-column>
				<el-table-column prop="name" label="轮播图名称" align="center"></el-table-column>
				<el-table-column prop="cname" label="机构名称" align="center"></el-table-column>
				<el-table-column prop="couname" label="课程名" align="center"></el-table-column>
				<el-table-column label="背景颜色" align="center" width="80">
				  <template slot-scope="scope" style="overflow: hidden;">
					  <div class="colorbg" :style="backgroundin(scope.row.color)"></div>
				  </template>
				</el-table-column>
				<el-table-column label="操作" width="220"  align="center">
				  <template slot-scope="scope" >
					<el-button
					  size="mini"
					  type="warning"
					  @click="handleEdit('2',scope.$index, scope.row)">编辑</el-button>
					<el-button
					  size="mini"
					  @click="handleOK(scope.$index, scope.row)">删除</el-button>
				  </template>
				</el-table-column>
			 </el-table>
			 <div class="block pagination">
			    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
				:current-page.sync="scinput.page" :page-size="16" layout="prev, pager, next, jumper" :total="num">
				</el-pagination>
			 </div>
		</div>
		<!-- 弹出窗 -->
		<el-dialog title="轮播图" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
		  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" label-position="top">
			<el-form-item label="轮播图(上传尺寸670*240,文件大小2M以内):" prop="img">
				<el-upload
				  action="https://zht.insooner.com/jg/v1/getUpdatePictures"
				  list-type="picture-card"
				  :file-list="ruleForm.img"
				  :on-preview="handlePictureCardPreview"
				  :on-success="handsuccess"
				  :on-remove="handleRemove">
				  <i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisibleimg">
				  <img width="100%" :src="dialogImageUrl" alt="">
				</el-dialog>
			</el-form-item>
			<el-form-item label="轮播图背景颜色" prop="color">
			  <el-color-picker v-model="ruleForm.color"></el-color-picker>
			</el-form-item>
			<el-form-item label="轮播图名称" prop="name">
			  <el-input size="large" v-model="ruleForm.name" placeholder="请输入轮播图名称"></el-input>
			</el-form-item>
		    <el-form-item label="选择机构" >
		      <el-select @change="SPselect()" v-model="ruleForm.member_id" clearable  size="large" style="width: 100%;" filterable placeholder="请选择机构">
				  <el-option
					v-for="item in options"
					:key="item.id"
					:label="item.cname"
					:value="item.id">
				  </el-option>
			  </el-select>
		    </el-form-item>
			<el-form-item label="机构课程">
			  <el-select v-model="ruleForm.course_id" :disabled="ruleForm.member_id==''?true:false" size="large" style="width: 100%;" filterable placeholder="请选择机构课程">
			      <el-option
			        v-for="item in optionsK"
			        :key="item.id"
			        :label="item.name"
			        :value="item.id">
			      </el-option>
			  </el-select>
			</el-form-item>
			<el-form-item label="热门排序" prop="sort">
			  <el-input size="large" type="number" v-model="ruleForm.sort" placeholder="请输入排序如:99"></el-input>
			</el-form-item>
			<el-form-item label="选择关联对象" prop="bann_type">
				<el-radio-group v-model="ruleForm.bann_type">
				    <el-radio :label="1">普通课程</el-radio>
				    <el-radio :label="2">机构</el-radio>
					<!-- <el-radio :label="3">活动</el-radio> -->
					<el-radio :label="4">综合体</el-radio>
					<el-radio :label="5">秒杀</el-radio>
					<el-radio :label="6">体验课</el-radio>
					<el-radio :label="7">社区活动</el-radio>
				  </el-radio-group>
			</el-form-item>
		    <el-form-item>
		      <el-button type="primary" @click="submitForm('ruleForm')">确 认</el-button>
		    </el-form-item>
		  </el-form>
		</el-dialog>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
			dialogImageUrl: '',
			dialogVisibleimg: false,
			//总页数
			num:1,
		  //获取的分类id
		  classid:null,
		  //提交内容
		  ruleForm: {
			img:[],
			name:'',
			member_id:'',
			course_id: '',
			sort: '',
			bann_type:'',
			color:null
		  },
		  rules: {
		  	sort: [{
		  		required: true,
		  		message: '请填写排序',
		  		trigger: 'blur'
		  	}],
		  	img: [{
		  		required: true,
		  		message: '上传图片',
		  		trigger: 'blur'
		  	}],
			color: [{
				required: true,
				message: '请选择颜色',
				trigger: 'blur'
			}],
		  },
		  dialogVisible: false,
		  //搜索内容
		  scinput:{
			  page:1
		  },
		  tableData: [],
		  // 搜索内容
		  options: [],
		  valueJ: [],
		  optionsK: [],
		  valueK:[]
	    }
	  },
	  methods: {
		//图片上传
		handleRemove(file, fileList) {
			console.log(file, fileList);
			this.ruleForm.img = fileList;
		},
		handlePictureCardPreview(file) {
		  this.dialogImageUrl = file.url;
		  this.dialogVisibleimg = true;
		  console.log(file);
		},
		// 上传图片成功
		handsuccess(file, fileList){
			console.log('上传成功',file, fileList);
			this.ruleForm.img.push(fileList);
		},
		SPselect(){
			this.KCAjaxlist();		
		},
		//添加修改
		submitForm(formName){
			this.$refs[formName].validate((valid) => {
			  if (valid) {
				let url = 'v1/addpcBannerImg';
				if (this.classid!=null) {
					url = 'v1/setpcBannerImg';
					this.ruleForm.banner_id = this.classid;
					this.KCAjaxlist();	
				};
				let that =this;
				let data=this.ruleForm;
				that.$post(url,data).then((response) => {
					console.log('成功',response);
					if (response.code==1000) {
						that.$message({
							type: 'success',
							message: '成功!'
						});
						that.dialogVisible = false;
						//过去列表
						this.ObjAjax();
					};
				});
			  } else {
				console.log('error submit!!');
				return false;
			  }
			});
		},
		//搜索
		scIN(){
			this.scinput.page = 1;
			this.ObjAjax();
		},
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
			this.scinput.page = val;
			//机构列表渲染
			this.ObjAjax();
		},
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			this.scinput.page = val;
			//机构列表渲染
			this.ObjAjax();
		},
		//编辑
		handleEdit(state,index, row) {
			// console.log(index, row); 
			//弹窗打开
			if (state=='2') {
				this.classid = row.id;
				this.ruleForm={
					img:row.img,
					name:row.name,
					member_id:row.member_id,
					course_id:row.course_id,
					sort:row.sort,
					bann_type:row.bann_type,
					color:row.color
				};
			}else{
				// 清空数据
				this.ruleForm={
					img:[],
					name:'',
					member_id:'',
					course_id: '',
					sort: '',
					bann_type:'',
					color:null
				};
				this.classid = null;
			};
			this.dialogVisible = true;
		},
		//同意
		handleOK(index, row) {
			console.log(index, row);
			this.$confirm('此操作将删除（'+row.name+'）分类, 是否继续?', '提示', {
			  confirmButtonText: '确定',
			  cancelButtonText: '取消',
			  type: 'warning'
			}).then(() => {
				let that =this;
				let data={
					id:row.id
				};
				that.$post('v1/delpcBanner',data).then((response) => {
					console.log('成功',response);
					if (response.code==1000) {
						that.$message({
							type: 'success',
							message: '删除成功!'
						});
						//机构列表渲染
						that.ObjAjax();
					};
				}); 
			}).catch(() => {
			  this.$message({
				type: 'info',
				message: '已取消'
			  });          
			});
		},
		handleClose(done) {
		  // 关闭窗
		  done();
		},
		//获取列表
		ObjAjax(){
			let that =this;
			let data=this.scinput;
			that.$post('v1/getpcBanner',data).then((response) => {
				console.log('成功',response);
				if (response.code==1000) {
					response.data.info.forEach(obj=>{
						obj.imges = obj.img[0].url;
					});
					this.tableData = response.data.info;
					this.num = response.data.num;
				};
				if (response.error_code==10000) {
					that.tableData=[];
				};
			}); 
		},
		// 获取机构列表
		JGAjaxlist(){
			let that =this;
			that.$post('v1/getpcHotMemberNane').then((response) => {
				// console.log('成功',response);
				if (response.code==1000) {
					this.options = response.data;
				};
			}); 
		},
		//获取课程信息
		KCAjaxlist(){
			let that =this;
			let data={
				mem_id:this.ruleForm.member_id
			};
			that.$post('v1/getpcHotCourseNane',data).then((response) => {
				console.log('课程信息成功',response);
				if (response.code==1000) {
					this.optionsK = response.data;
				};
			}); 
		},
		//背景颜色
		backgroundin(e){
			return 'background-color:'+e
		}
	  },
	  mounted() {
	  	this.ObjAjax();
		this.JGAjaxlist();
	  }
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
			.buttonRight{
				float: right;
			}
		}
		.InBoxBody-con{
			width: 100%;
			overflow: hidden;
			.InBoxBody-con-input{
				width: 100%;
				overflow: hidden;
				margin-bottom:20px;
				span{
				   margin-left: 20px;
				   color: @color4;
				   font-size: 16px;
				   float: left;
				   line-height: 32px;
				};
				.input{
					float: left;
					width: 200px;
					height: 32px;
					margin-left: 12px;
				};
				.time{
					float: left;
					height: 32px;
					margin-left: 12px;
				}
			};
			.pagination{
				float: right;
				margin: 20px;
			}
			.logo{
				width: 160px;
				height: 50px;
				display: block;
				float: left;
				margin-top: 4px;
			}
			.logours{
				float: left;
				margin-left: 10px;
				width:80px;
				text-align: left;
				div{
					line-height: 60px;
				}
			}
		}
	}
	
	.avatar-uploader .el-upload {
	    border: 1px dashed #d9d9d9;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	  }
	  .avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	  }
	  .avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 120px;
	    height: 120px;
	    line-height: 120px;
	    text-align: center;
		border: 1px dashed #d9d9d9;
	  }
	  .avatar {
	    width: 120px;
	    height: 120px;
	    display: block;
	  }
	 .dialoginput{
		 width: 100%;
		 margin: 20px 0;
		 overflow: hidden;
		 .dialoginputFl{
			 width:calc(50% - 10px);
			 margin-right: 10px;
			 float: left;
		 }
	 }
	 .colorbg{
		width: 40px;
		height: 40px;
		margin: 0 auto;
	 }
</style>



