<template>
	<div id="container"></div>
</template>
<script>
	// import { TMap } from '../../store/TMap'
	export default {
		name: 'news',
		data() {
			return {
				longitude: 0, //经度
				latitude: 0, //纬度
				city: ''
			}
		},
		methods: {
			//位置信息在地图上展示
			setMap(data) {
				//设置地图中心点
				var myLatlng = new qq.maps.LatLng(30.187917, 120.192951);
				var Map = qq.maps.Map;
				var Marker = qq.maps.Marker;
				var LatLng = qq.maps.LatLng;
				var Event = qq.maps.event;

				var MarkerImage = qq.maps.MarkerImage;
				var MarkerShape = qq.maps.MarkerShape;
				var MarkerAnimation = qq.maps.MarkerAnimation;
				var Point = qq.maps.Point;
				var Size = qq.maps.Size;
				var ALIGN = qq.maps.ALIGN;

				var MVCArray = qq.maps.MVCArray;
				var MarkerCluster = qq.maps.MarkerCluster;
				var Cluster = qq.maps.Cluster;
				var MarkerDecoration = qq.maps.MarkerDecoration;
				//定义工厂模式函数
				var myOptions = {
					zoom: 13, //设置地图缩放级别
					center: myLatlng, //设置中心点样式
					mapTypeId: qq.maps.MapTypeId.ROADMAP //设置地图样式详情参见MapType
				}
				// //获取dom元素添加地图信息
				var map = new qq.maps.Map(document.getElementById("container"), myOptions);
				var markers = new MVCArray();
				var markerCluster;
				//聚合点设置
				function createCluster() {
					data.forEach(obj => {
						var anchor = new qq.maps.Point(6, 6),
							size = new qq.maps.Size(24, 24),
							origin = new qq.maps.Point(0, 0),
							icon = new qq.maps.MarkerImage('https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/center.gif', size, origin, anchor);
						//给定位的位置添加图片标注
						var latLng = new LatLng(obj.latitude, obj.longitude);
						var marker = new Marker({
							'position': latLng,
							icon: icon,
							map: map
						});
						//添加到提示窗
						var info = new qq.maps.InfoWindow({
							map: map
						});
						//获取标记的点击事件
						qq.maps.event.addListener(marker, 'click', function() {
							info.open(); 
							info.setContent('<img src="'+obj.logo+'" style="width:200px;height:200px;display: block;" alt=""><div style="text-align:center;white-space:nowrap;'+
							'margin:2px;">'+obj.cname+'</div>');
							info.setPosition(latLng); 
						});
						markers.push(marker);
					});
					var markerClusterer = new MarkerCluster({
						map: map,
						minimumClusterSize: 2, //默认2
						markers: markers,
						zoomOnClick: true, //默认为true
						gridSize: 30, //默认60
						averageCenter: true, //默认false
						maxZoom: 18, //默认18
					});
					//点击聚合点
					Event.addListener(markerClusterer, 'clusterclick', function(evt) {
						// console.log('聚合点',evt);
					});
				};
				createCluster();
				
			},
			mapajax() {
				this.$post('v1/getpcLongitudeLatitude').then((response) => {
					console.log(response);
					if (response.code == 1000) {
						this.setMap(response.data);
					};
				});
			}
		},
		mounted() {
			this.mapajax()
		}
	}
</script>
<style>
	#container {
		width: 100%;
		height: 100%;
	}
</style>



