<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
			  <el-breadcrumb-item :to="{ path: '/CompList' }">成长中心信息</el-breadcrumb-item>
			  <el-breadcrumb-item>成长中心信息编辑</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con">
			<el-divider content-position="left">成长中心信息</el-divider>
			<div class="InBoxBody-con-input">
				<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
					<div class="InBoxBody-con-input-style">
						<el-form-item label="选择教室" prop="valjs" style="float: left;">
							<el-cascader
								class="flat"
								clearable
							    v-model="ruleForm.valjs"
							    :options="optionsZHTjs"
							    :props="{ expandTrigger: 'hover' }"></el-cascader>
						</el-form-item>						
					</div>
					<!-- <div class="InBoxBody-con-input-style" style="margin-top: 20px;">
						<el-form-item label="日期选择:" prop="timeis">
							<div class="block" style="float: left;">
								<el-date-picker
								  @change="timestate()"
								  v-model="ruleForm.timeis"
								  type="daterange"
								  value-format="timestamp"
								  range-separator="至"
								  start-placeholder="开始日期"
								  end-placeholder="结束日期">
								</el-date-picker>
							</div>
							<el-button style="width:80px;margin-left:20px;float: left;" type="warning" plain @click="Schedule()">生成课表</el-button>
						</el-form-item>
					</div> -->
					<!-- 生成课表 -->
					<!-- <div :hidden="ScheduleSTATE">
						<div class="InBoxBody-con-input-style" style="margin-top: 20px;">
							<el-form-item label="批量时间:">
								<div class="timebox">
									<div class="timeboxstyle">
										<div class="timeboxstyle-tit">上午</div>
										<el-checkbox-group v-model="selection_array.morning">
											<el-checkbox class="kbox"  v-for="(item,index) in timeAll.morning" :key="index" :disabled="item.selection!=2?true:false" :label="item.time_slot">{{timesw(index,6)}}</el-checkbox>
										</el-checkbox-group>
									</div>
									<div class="timeboxstyle">
										<div class="timeboxstyle-tit">下午</div>
										<el-checkbox-group v-model="selection_array.afternoon">
											<el-checkbox class="kbox"  v-for="(item,index) in timeAll.afternoon" :disabled="item.selection!=2?true:false" :key="index" :label="item.time_slot">{{timesw(index,12)}}</el-checkbox>
										</el-checkbox-group>
									</div>
									<div class="timeboxstyle">
										<div class="timeboxstyle-tit">晚上</div>
										<el-checkbox-group v-model="selection_array.night">
											<el-checkbox class="kbox"  v-for="(item,index) in timeAll.night" :disabled="item.selection!=2?true:false" :key="index" :label="item.time_slot">{{timesw(index,18)}}</el-checkbox>
										</el-checkbox-group>
									</div>
								</div>
							</el-form-item>
						</div>
					</div> -->
					<div class="InBoxBody-con-input-style" style="margin-top: 20px;">
						<el-button style="margin-left:90px;width:80px;" type="warning" @click="submitForm('ruleForm')">确 认</el-button>
						<el-button style="width:80px;" type="warning" plain @click="$router.go(-1)">取 消</el-button>
					</div>
				</el-form>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
		//课表状态
		ScheduleSTATE: true,
		//统一设置
		timeAll: {
			morning:[],
			afternoon:[],
			night:[]
		},
		selection_array:{
			morning:[],
			afternoon:[],
			night:[]
		},
		optionsZHTjs: [], //成长中心教室分类
		ruleForm: {
			valjs: '',
			timeis: '', //时间选择
			//成长中心数据结构
			selection_array:{
				morning:[],
				afternoon:[],
				night:[]
			}
		},
		rules: {
			valjs: [{
				required: true,
				message: '请输选择成长中心教室',
				trigger: 'change'
			}, ],
			timeis: [{
				required: true,
				message: '请选择日期',
				trigger: 'blur'
			}]
		},
	}
	  },
	  methods: {
		//时间输入发生改变
		timestate(){
			this.ScheduleSTATE= true;
			// 清空选择
			this.checkListsw=[];//上午
			this.checkListxw=[]; //下午
			this.checkListws=[]; //晚上
		},
		//课表生成
		Schedule(){
			console.log(this.ruleForm.timeis);
			if (this.ruleForm.timeis=='') {
				this.$message({
				type: 'info',
				message: '请选择日期'
				});
			}else if(this.ruleForm.valjs==''){
				this.$message({
				type: 'info',
				message: '请选择成长中心教室'
				});
			}else{
				this.objtimethis();
			}
		},
		//时间转换
		timesw(index,time){
			let a,b,c,d;
			if (index%2 ==0) {
				a=(index/2)*1+time;
				c=(index/2)*1+time;
				b = '00';
				d = '30';
			} else{
				a=(index/2).toFixed(0)*1+time-1;
				c=(index/2).toFixed(0)*1+time;
				b = '30';
				d = '00';
			}
			return a+':'+b+'---'+c+':'+d
		},
		//提交
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
			  if (valid) {
				let that = this;
				let data = {
					course_id:that.$route.query.mid,
					start_time:that.ruleForm.timeis[0],
					end_time:that.ruleForm.timeis[1],
					valjs:that.ruleForm.valjs,
					selection_array:that.ruleForm.selection_array
				};
				let tjurl = 'v1/setpcSynthetical';
				that.$post(tjurl,data).then((response) => {
					console.log('成功',response);
					if (response.code==1000) {
						that.$message({
							message: '提交成功',
							type: 'success'
						});
						that.$router.go(-1);
					};
				});
			  } else {
				console.log('error submit!!');
				return false;
			  }
			});
		},
		// 获取课目分类
		objzht(){
			let that =this;
			that.$post('v1/getpcSyntheticalClassroomType').then((response) => {
				// console.log('成功',response);
				if (response.code==1000) {
					this.optionsZHTjs = response.data
				};
			});
		},
		//选择时间后获取关系
		objtimethis(){
			this.ScheduleSTATE = false;
			let that =this;
			let data={
				start_time:that.ruleForm.timeis[0],
				end_time:that.ruleForm.timeis[1],
				valjs:that.ruleForm.valjs,
				course_id:this.$route.query.mid,
				classroom_type:2
			};
			that.$post('v1/isTimereturn',data).then((response) => {
				// console.log('成功',response);
				if (response.code==1000) {
					that.timeAll = response.data.format_array;
					that.ruleForm.selection_array = response.data.selection_array;
					that.selection_array = that.ruleForm.selection_array;
				};
			});
		},
		// 获取详情
		ObjectCon(){
			let that =this;
			let data={
				course_id:this.$route.query.mid,
			};
			that.$post('v1/getpcSyntheticaldetails',data).then((response) => {
				console.log('成功',response);
				if (response.code==1000) {
					let timeis = [];
					timeis.push(response.data.start_time);
					timeis.push(response.data.end_time);
					response.data.timeis = timeis;
					this.ruleForm = response.data;
					if (response.data.start_time!=null) {
						that.objtimethis();
					}else{
						that.ruleForm.timeis = '';
					}
				};
			});
		}
	  },
	  mounted() {
	  	this.objzht();
		//是否存在id
		this.ObjectCon();
	  },
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//颜色样式
	.InBoxBody{
			width: 100%;
			border:@borderbox;
			background:@bgcolor;
			border:1px solid @bordersolid;
			border-radius:10px;	
			overflow: hidden;
			.InBoxBody-header{
				width:calc(100% - 40px);
				padding: 20px 20px;
				.InBoxBody-header-text{
					padding-left:6px;
					font-size: 16px;
					border-left:@borderliftsolid;
				}
			}
			.InBoxBody-con{
				width: 100%;
				.InBoxBody-con-tit{
					padding: 20px;
					color:@color4;
					border-bottom: @borderbox;
				}
				.InBoxBody-con-input{
					width: 100%;
					overflow: hidden;
					padding: 0 0 20px 0;
					.InBoxBody-con-input-style{
						width:calc(100% - 40px);
						padding: 20px 20px 0 20px;
						overflow: hidden;
						.InBoxBody-con-input-tit{
							float: left;
							line-height:32px;
							font-size: 14px;
							width: 90px;
						}
						.moenycor{
							color:@color2;
						}
						.flat{
							float: left;
							width: 200px;
							margin-right: 40px;
						}
						.flattextarea{
							float: left;
							width:80%;
							margin-right: 40px;
						}
					}
				}
			}
		}
		.timebox{
			width:80%;
			overflow: hidden;
			border: @borderbox;
			border-radius: 6px;
			padding: 20px;
			.timeboxstyle{
				overflow: hidden;
				float: left;
				margin-bottom: 20px;
				.timeboxstyle-tit{
					font-size: 14px;
				}
				.kbox{
					width: 150px;
					float: left;
					margin: 16px 0 0 0;
				}
			};
		}
</style>
<style>
  .is-selected {
    color: #1989FA;
  }
</style>


