<template>
	<div class="InBoxBody">
		<div class="InBoxBody-con">
			<!-- 数据 -->
			<div class="InBoxBody-header">
				<span class="InBoxBody-header-text">用户分析</span>
			</div>
			<div class="hometopone">
				<div class="homelift-top">
					<div class="homelift-top-con">	
						<el-row>
							<el-col :span="12">
								<div style="position: relative;margin-top: 20px;">
									<span class="homelift-top-con-one">83</span>
									<div class="homelift-top-con-K"></div>
								</div>
								<div><span class="homelift-top-con-two">用户总量</span></div>
							</el-col>
							<el-col :span="12">
								<div style="position: relative;margin-top: 20px;">
									<span class="homelift-top-con-one" style="background-color: #FA9B5B;">78979.000</span>
									<div class="homelift-top-con-K" style="background-color: #FA9B5B;"></div>
								</div>
								<div><span class="homelift-top-con-two">用户消费总额</span></div>
							</el-col>
						</el-row>
					</div>
				</div>
			</div>
			<!-- 数据分析 -->
			<div class="InBoxBody-header">
				<span class="InBoxBody-header-text">数据分析</span>
				<div class="InBoxBody-con-input">
					<span>信息搜索</span>
					<div class="block time">
						<el-date-picker v-model="time" type="daterange" align="right" unlink-panels range-separator="至" start-placeholder="开始日期"
						 end-placeholder="结束日期" :picker-options="pickerOptions">
						</el-date-picker>
					</div>
					<el-button class="time" type="warning" plain>搜 索</el-button>
				</div>
				<div class="Dataanalysiscon-con">
					<div class="Dataanalysiscon-con-text">
						<div id="maintwo" style="width:100%;height:300px;margin-bottom: 43px;"></div>
					</div>
				</div>
			</div>
			<!-- 详细数据 -->
			<div class="InBoxBody-header">
				<span class="InBoxBody-header-text">详细数据</span>	
			</div>
			<el-table :data="tableData" style="width: 100%" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
				<el-table-column prop="date" label="日期" align="center">
				</el-table-column>
				<el-table-column prop="name" label="用户消费" align="center">
				</el-table-column>
				<el-table-column prop="tk" label="增长数量" align="center">
				</el-table-column>
				<el-table-column prop="address" label="退款" align="center">
				</el-table-column>
			</el-table>
			<!-- 分页 -->
			<el-pagination
			style="float: right;margin: 30px 30px 30px 0;"
			  @size-change="handleSizeChange"
			  @current-change="handleCurrentChange"
			  :current-page.sync="currentPage"
			  :page-size="100"
			  layout="prev, pager, next, jumper"
			  :total="1000">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	var echarts = require('echarts');
	export default {
		data() {
			return {
				currentPage: 1,
				echartslist: {
					time: ['5月', '6月', '7月', '8月', '9月', '10月', '11月'],
					data: [10, 52, 200, 334, 390, 330, 220]
				},
				tel: '',
				time: '',
				pickerOptions: {
					shortcuts: [{
						text: '最近一周',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
							picker.$emit('pick', [start, end]);
						}
					}, {
						text: '最近一个月',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
							picker.$emit('pick', [start, end]);
						}
					}, {
						text: '最近三个月',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
							picker.$emit('pick', [start, end]);
						}
					}]
				},
				tableData: [{
					date: '2016-05-02',
					name: '123',
					tk:'456456',
					address: '45546'
				},{
					date: '2016-05-02',
					name: '123',
					tk:'456456',
					address: '45546'
				},{
					date: '2016-05-02',
					name: '123',
					tk:'456456',
					address: '45546'
				},{
					date: '2016-05-02',
					name: '123',
					tk:'456456',
					address: '45546'
				}]
			}
		},
		methods: {
			echartsTB() {
				let that = this;
				// 基于准备好的dom，初始化echarts实例
				var myChart = echarts.init(document.getElementById('maintwo'));
				// 绘制图表
				myChart.setOption({
					tooltip: {
						trigger: 'axis'
					},
					xAxis: {
						type: 'category',
						data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
						axisLine: {
							show: false
						}
					},
					grid: {
						left: '2%',
						right: '4%',
						bottom: '3%',
						top: '2%',
						containLabel: true
					},
					yAxis: {
						type: 'value',
						axisLine: {
							show: false
						}
					},
					series: [{
						data: [820, 932, 901, 934, 1290, 1330, 1320],
						type: 'line',
						smooth: true,
						itemStyle: {
							normal: {
								color: '#f7c367'
							}
						},
					}]
				});
			},
		handleSizeChange(val) {
		       console.log(`每页 ${val} 条`);
		     },
		     handleCurrentChange(val) {
		       console.log(`当前页: ${val}`);
		     }
		},
		mounted() {
			this.echartsTB();
		},
	}
</script>

<style scoped lang="less">
	@import '../../assets/style/index.less'; //颜色样式

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con {
			width: 100%;
		}
	}

	.homelift-top {
		width: 100%;

		.homelift-top-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.homelift-top-header-text {
				border-left: @borderliftsolid;
				padding-left: 6px;
				font-size: 16px;
			}

			.homelift-top-header-more {
				float: right;
				color: @color4;
				font-size: 16px;
				cursor: pointer;
			}
		}

		.homelift-top-con {
			width: 100%;
			text-align: center;

			.homelift-top-con-one {
				background-color: #F4C75A;
				color: #fff;
				font-size: 32px;
				border-radius: 12px;
				padding: 4px 18px;
			}

			.homelift-top-con-two {
				font-size: 16px;
				line-height: 60px;
				margin-bottom: 40px;
			}

			.homelift-top-con-three {
				font-size: 16px;
				color: @color4;
				line-height: 30px;

				i {
					color: @color5;
					font-style: normal;
				}
			}
		}
	}

	.InBoxBody-con-input {
		width: 100%;
		overflow: hidden;
		margin-bottom: 20px;
		margin-top: 20px;

		span {
			color: @color4;
			font-size: 16px;
			float: left;
			line-height: 32px;
		}

		;

		.input {
			float: left;
			width: 200px;
			height: 32px;
			margin-left: 12px;
		}

		;

		.time {
			float: left;
			height: 32px;
			margin-left: 12px;
		}

		.buttonRight {
			float: right;
		}
	}

	;

	.Dataanalysiscon-con {
		width: 100%;
		margin-top: 50px;

		.Dataanalysiscon-con-text {
			width: calc(100% - 40px);
			padding: 0 20px;
		}
	}
</style>



